.card img{
    width:15vw;
    display:flex;
    
    
    background:rgb(255, 254, 254);
    margin-top: 45px;
    cursor: pointer;
    border: 4px rgb(99, 99, 99) solid;
    border-radius: 2px;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    transition:all 0.25s ease-in-out;
}

.card img:hover{
    background:rgb(195, 195, 195);
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
}

.card img:active{
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
}



#brand, #current_score, #topScore{
    color:white;
    padding: 0 30px;
}
#rightOrWrong{
    color:rgb(0, 173, 253);
    font-weight: bold;
    font-size: 20px;
    transition:all 0.25s ease-in-out;
    margin-left:-5px;
}
nav {
    
    display:flex;
    margin: auto;
    background:rgba(0, 0, 0, 0.7);
    
}
ul {
    margin: auto;
    padding:5px;
    font-size: 16px;
    text-align:center;
}



@media only screen and (max-width: 414px) {
    
    #rightOrWrong{
        color:rgb(0, 173, 253);
        font-weight: bold;
        font-size: 20px;
        -webkit-animation:moveInOut 0.25s ease-in-out;
                animation:moveInOut 0.25s ease-in-out;
        margin-left:-5px;
    }
    @-webkit-keyframes moveInOut {
        from {
            -webkit-transform:scale(1);
                    transform:scale(1)
        }
        to {
            -webkit-transform:scale(1.5);
                    transform:scale(1.5)
        }
        from{
            -webkit-transform: scale(1);
                    transform: scale(1)
        }
    }
    @keyframes moveInOut {
        from {
            -webkit-transform:scale(1);
                    transform:scale(1)
        }
        to {
            -webkit-transform:scale(1.5);
                    transform:scale(1.5)
        }
        from{
            -webkit-transform: scale(1);
                    transform: scale(1)
        }
    }
    nav {
        width: 200px;
        height: 200px;
        border-radius:100px;
        display:flex;
        margin: auto;
        background:rgba(0, 0, 0, 0.7);
        
    }
    ul {
        margin: auto;
        font-size: 16px;
        text-align:center;
    }
}
